define('uiux/routes/docks/batch', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var DocksBatchRoute;

  DocksBatchRoute = Ember['default'].Route.extend({
    model: function model(arg) {
      var id;
      id = arg.batchId;
      return this.store.find("batch", id);
    }
  });

  exports['default'] = DocksBatchRoute;

});