define('uiux/routes/docks', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var DocksRoute;

  DocksRoute = Ember['default'].Route.extend({
    model: function model() {
      return this.modelFor("application").filterBy("tileType", "barn");
    }
  });

  exports['default'] = DocksRoute;

});